@import url('../../gloabls.css');
:host {
  display: block;
}
.page-loader {
  width: 30px;
  aspect-ratio: 1;
  border-radius: 50%;
  background:
    radial-gradient(farthest-side, #000000 94%, #0000) top/4px 4px no-repeat,
    conic-gradient(#0000 30%, #000000);
  mask: radial-gradient(farthest-side, #0000 calc(100% - 4px), #000 0);
  -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 4px), #000 0);
  animation: l13 1s infinite linear;
}
.ir-auth-nav {
  width: 100%;
  background-color: white;
}

.ir-auth-nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 72rem;
  min-height: 3.5rem;
  padding: 0 1rem;
  margin: 0 auto;
}
.ir-table-cell,
.ir-table-head {
  padding: 0.75rem 0.2rem;
  width: max-content !important;
}
.bl-back-btn:dir(rtl) {
  transform: rotate(180deg);
}
.ir-nav-logo {
  /* height: 2.5rem;
  width: auto;
  */
  /* width: 40px; */
  /* max-height: 5rem; */
  max-height: 4.2rem;
  padding: 0.5rem;
  object-fit: contain;
}
.main-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  gap: 1rem;
}
.header-left {
  display: flex;
  align-items: center;
  gap: 0.625rem;
}

.header-title {
  font-size: 1.125rem;
  font-weight: 600;
  padding: 0;
  margin: 0;
}

@media (min-width: 768px) {
  .main-container.main-container-padding {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
@media (min-width: 1024px) {
  .main-container.main-container-padding {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}
@media (min-width: 1280px) {
  .main-container.main-container-padding {
    padding-left: 0px;
    padding-right: 0px;
  }
}
@keyframes l13 {
  100% {
    transform: rotate(1turn);
  }
}
